<template>
    <div class="addDialog">
        <el-dialog
          :title="dialog.dialogTitle+''"
          :visible.sync="dialog.show"
          :close-on-click-modal="false"
          :append-to-body="false">
        <div class="form">
            <el-form ref="form"
             :model="formData"
             :rules="form_rules"
             label-width="120px"
             style="margin:10px;width:auto">
                <el-form-item
                 label=":">
                    <el-select v-model="formData.type" placeholder="">
                        <el-option
                         v-for="(formtype,index) in format_type_list" 
                         :key="index"
                         :value="formtype"
                        >
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item
                 prop="describe"
                 label=":">
                    <el-input type="describe" v-model="formData.describe">
                    </el-input>
                </el-form-item>
                <el-form-item
                 prop="income"
                 label=":">
                    <el-input type="income" v-model="formData.income">
                    </el-input>
                </el-form-item>
                <el-form-item
                 prop="expend"
                 label=":">
                    <el-input type="expend" v-model="formData.expend">
                    </el-input>
                </el-form-item>
                <el-form-item
                 prop="cash"
                 label=":">
                    <el-input type="cash" v-model="formData.cash">
                    </el-input>
                </el-form-item>
                <el-form-item
                 prop="remark"
                 label=":">
                    <el-input type="remark" v-model="formData.remark">
                    </el-input>
                </el-form-item>
                <el-form-item class="text-right">
                    <el-button @click="dialog.show=false"></el-button>
                    <el-button type="primary" @click="onSubmit('form')"></el-button>
                </el-form-item>
            </el-form>
        </div>
        </el-dialog>
    </div>
</template>


<script>
export default {
    name: 'Dialog',
    data(){
        return {
            format_type_list:[
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            form_rules: {
                describe:[
                    {required: true, message: "", trigger:"blur"}
                ],
                income:[
                    {required: true, message: "", trigger:"blur"}
                ],
                expend:[
                    {required: true, message: "", trigger:"blur"}
                ],
                cash:[
                    {required: true, message: "", trigger:"blur"}
                ],
                remark:[
                    {required: true, message: "", trigger:"blur"},
                ]
            }

        }
    },
    props:{
        dialog: Object,
        formData: Object
    },
    methods:{
        onSubmit(form){
            this.$refs[form].validate(valid=>{
                if(valid){
                    this.$emit("submit");
                    this.dialog.show = false;
                }
            });
        }
    }
};
</script>

<style scoped>
.addDialog{
    text-align: left;
}
</style>


