<template>
    <div class="login">
        <section class="form_container">
            <div class="manage_tip">
                <span class="title">
                    
                    <el-form :model="loginUser"  :rules="rules" ref="loginForm" label-width="100px" class="login_form">
                        <el-form-item label="" prop="email">
                            <el-input v-model="loginUser.email" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input type="password" v-model="loginUser.password" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" class="submit_btn" @click="submitForm('loginForm')"></el-button>
                        </el-form-item>
                       <div class="tiparea">
                           <p><router-link to='/register'></router-link></p>
                       </div>
                    </el-form>
                </span>
            </div>
        </section>
    </div>
</template>


<script>
export default {
    name: 'login',
    components: {},
    data() {
        return {
            loginUser: {
                email: "",
                password: ""
            },
            rules: {
                email: [
                    {
                        type:"email",required:true,message:"",trigger:"blur"
                    },
                ],
                password: [
                    {
                        required:true,message:"",trigger:"blur"
                    },
                    {
                        min:6,max:30,message:"630",trigger:"blur"
                    }
                ]
            }
        }
    },
    methods:{
        submitForm(formName){
          this.$refs[formName].validate((valid) => {
            var baseUrl='https://www.easy-mock.com/mock/5bc7014ed85ce204b23a8f4d/vn/user/login';
            if (valid) {
                this.$axios.post(baseUrl).then(res => {
                    //token
                    const { eletoken } = res.data;
                    console.log(eletoken);
                    //ls
                    localStorage.setItem('eleToken',eletoken);
                    //token
                    //const decode = jwt_decode(token);
                    //tokenvuex
                    var userInfo = eletoken.split("_");
                    var user = {
                        name: userInfo[0],
                        email: this.loginUser.email,
                        password: this.loginUser.password,
                        avatar: "../assets/logo.png"
                    }
                    this.$store.dispatch("setUser",user);
                    this.$store.dispatch("setAutheticated",!this.isEmpty(eletoken));
                    this.$router.push('/index');
                });
            } else {
                console.log('error submit!!');
                return false;
            }
            });
        },
        isEmpty(value){
            return (
                value === undefined ||
                value === null ||
                (typeof value === "object" && Object.keys(value).length === 0)||
                (typeof value === "string" && value.trim().length === 0)
            );
        }
    },
    computed:{
        password() {
            return this.loginUser.password;
        }
    },
    watch: {
        password: function(newValue,oldValue){
            this.loginUser.password2 = "";
        }
    }
};
</script>
